#modal-modal-title:hover {
  background-color: hsla(0, 0%, 76%, 0.14);
}
#modal-modal-title {
  background-color: #fff;
  transition: background-color 0.2s ease-in-out 0s;
  padding: 20px;
  border-radius: 5px;
}
